
#app {
	font-family: Avenir, Helvetica, Arial, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	text-align: center;
	color: #2c3e50;
	margin-top: 60px;

	main{
		display: grid;
		grid-template-areas:
			'monster-list monster-details monster-details';
	}

	.monster-details{
		grid-area: monster-details;
	}
}
